<template>
  <view class="container">
    <view class="chat-header">
      <!-- ‰ΩøÁî®ËÉåÊôØËâ≤‰ª£ÊõøÂõæÁâá -->
      <view class="ai-banner-placeholder"></view>
      <view class="title">Êô∫ËÉΩÊãõÁîüÂí®ËØ¢</view>
      <view class="subtitle">Áî±AIÈ©±Âä®Ôºå‰∏∫ÊÇ®Ëß£Á≠îÊãõÁîüÁõ∏ÂÖ≥ÈóÆÈ¢ò</view>
    </view>
    
    <scroll-view class="chat-container" scroll-y="true" :scroll-top="scrollTop" @scrolltoupper="loadMore">
      <view class="chat-list">
        <view class="time-divider">{{ currentDate }}</view>
        
        <!-- Á≥ªÁªüÊ∂àÊÅØ -->
        <view class="chat-item system">
          <text class="message">ÊÇ®Â•ΩÔºåÊàëÊòØÂπøÂ∑ûËà™Êµ∑Â≠¶Èô¢ÊãõÁîüÊô∫ËÉΩÂí®ËØ¢Âä©ÊâãÔºåËØ∑ÈóÆÊúâ‰ªÄ‰πàÂèØ‰ª•Â∏ÆÂä©ÊÇ®ÁöÑÂêóÔºüÊÇ®ÂèØ‰ª•ËØ¢ÈóÆÊãõÁîüÊîøÁ≠ñ„ÄÅ‰∏ì‰∏ö‰ø°ÊÅØ„ÄÅÂΩïÂèñÊ†áÂáÜÁ≠âÈóÆÈ¢ò„ÄÇ</text>
        </view>
        
        <!-- ËÅäÂ§©ËÆ∞ÂΩï -->
        <block v-for="(item, index) in chatMessages" :key="index">
          <!-- Áî®Êà∑Ê∂àÊÅØ -->
          <view class="chat-item user" v-if="item.sender === 'user'">
            <view class="message">{{ item.content }}</view>
            <view class="avatar">
              <view class="avatar-placeholder user-avatar-placeholder"></view>
            </view>
          </view>
          
          <!-- AI ÂõûÂ§ç -->
          <view class="chat-item ai" v-else>
            <view class="avatar">
              <view class="avatar-placeholder ai-avatar-placeholder">
                <text class="chat-icon">üí¨</text>
              </view>
            </view>
            <view class="message">{{ item.content }}</view>
          </view>
        </block>
        
        <!-- AI Ê≠£Âú®ËæìÂÖ•ÊèêÁ§∫ -->
        <view class="chat-item ai" v-if="aiTyping">
          <view class="avatar">
            <view class="avatar-placeholder ai-avatar-placeholder">
              <text class="chat-icon">üí¨</text>
            </view>
          </view>
          <view class="message typing">
            <view class="typing-animation">
              <view class="dot"></view>
              <view class="dot"></view>
              <view class="dot"></view>
            </view>
          </view>
        </view>
      </view>
    </scroll-view>
    
    <view class="input-container">
      <view class="hot-questions" v-if="showHotQuestions">
        <view class="hot-title">Â∏∏ËßÅÈóÆÈ¢ò</view>
        <view class="question-list">
          <view class="question-item" v-for="(question, index) in hotQuestions" :key="index" @tap="selectHotQuestion(question)">
            {{ question }}
          </view>
        </view>
      </view>
      
      <view class="input-wrapper">
        <input 
          class="chat-input" 
          v-model="userInput" 
          placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈóÆÈ¢ò" 
          confirm-type="send"
          @confirm="sendMessage"
          @focus="onInputFocus"
          @blur="onInputBlur"
        />
        <view class="send-btn" :class="{ active: userInput.trim().length > 0 }" @tap="sendMessage">ÂèëÈÄÅ</view>
      </view>
    </view>
  </view>
</template>

<script>
import aiAPI from '@/api/ai.js';

export default {
  data() {
    return {
      userInput: '',
      scrollTop: 0,
      aiTyping: false,
      showHotQuestions: true,
      currentDate: '',
      chatMessages: [],
      hotQuestions: [
        'ÂπøÂ∑ûËà™Êµ∑Â≠¶Èô¢ÊúâÂì™‰∫õÁéãÁâå‰∏ì‰∏öÔºü',
        '‰ªäÂπ¥ÊãõÁîüËÆ°ÂàíÂíåÂΩïÂèñÂàÜÊï∞Á∫øÊòØÂ§öÂ∞ëÔºü',
        'Â≠¶Ê†°ÊúâÂì™‰∫õÂ•ñÂ≠¶ÈáëÊîøÁ≠ñÔºü',
        'Ëà™Êµ∑Á±ª‰∏ì‰∏öÂ∞±‰∏öÂâçÊôØÊÄé‰πàÊ†∑Ôºü',
        'ÂèØ‰ª•Ë∞ÉÂâÇÂà∞ÂÖ∂‰ªñ‰∏ì‰∏öÂêóÔºü'
      ]
    }
  },
  onLoad() {
    this.setCurrentDate();
    uni.setNavigationBarTitle({
      title: 'Êô∫ËÉΩÂí®ËØ¢'
    });
  },
  methods: {
    setCurrentDate() {
      const date = new Date();
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      this.currentDate = `${year}Âπ¥${month}Êúà${day}Êó•`;
    },
    sendMessage() {
      if (this.userInput.trim() === '') return;
      
      // Ê∑ªÂä†Áî®Êà∑Ê∂àÊÅØ
      this.chatMessages.push({
        sender: 'user',
        content: this.userInput,
        time: new Date().getTime()
      });
      
      // Ê∏ÖÁ©∫ËæìÂÖ•Ê°Ü
      const userQuestion = this.userInput;
      this.userInput = '';
      
      // Ëá™Âä®ÊªöÂä®Âà∞Â∫ïÈÉ®
      this.$nextTick(() => {
        this.scrollToBottom();
      });
      
      // ÊòæÁ§∫AIÊ≠£Âú®ËæìÂÖ•Áä∂ÊÄÅ
      this.aiTyping = true;
      
      // Ë∞ÉÁî®ÂêéÁ´ØAIÊé•Âè£Ëé∑ÂèñÂõûÁ≠î
      aiAPI.askQuestion(userQuestion)
        .then(res => {
          this.aiTyping = false;
          
          // Ê∑ªÂä†AIÂõûÂ§ç
          this.chatMessages.push({
            sender: 'ai',
            content: res.data,
            time: new Date().getTime()
          });
          
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          this.$nextTick(() => {
            this.scrollToBottom();
          });
        })
        .catch(err => {
          this.aiTyping = false;
          
          // Ê∑ªÂä†ÈîôËØØÊèêÁ§∫
          this.chatMessages.push({
            sender: 'ai',
            content: 'Êä±Ê≠âÔºåAIÂõûÁ≠îÁîüÊàêÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï„ÄÇ',
            time: new Date().getTime()
          });
          
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          this.$nextTick(() => {
            this.scrollToBottom();
          });
          
          console.error('AIÂõûÁ≠îÁîüÊàêÂ§±Ë¥•:', err);
        });
    },
    scrollToBottom() {
      // Ëé∑ÂèñËäÇÁÇπ‰ø°ÊÅØÔºåËÆ°ÁÆóÊªöÂä®‰ΩçÁΩÆ
      const query = uni.createSelectorQuery().in(this);
      query.select('.chat-list').boundingClientRect(data => {
        if (data) {
          this.scrollTop = data.height * 2; // ÊªöÂä®Âà∞Â∫ïÈÉ®
        }
      }).exec();
    },
    loadMore() {
      // Âä†ËΩΩÊõ¥Â§öÂéÜÂè≤Ê∂àÊÅØ
      console.log('Âä†ËΩΩÊõ¥Â§öÂéÜÂè≤Ê∂àÊÅØ');
    },
    selectHotQuestion(question) {
      this.userInput = question;
      this.sendMessage();
    },
    onInputFocus() {
      this.showHotQuestions = false;
    },
    onInputBlur() {
      // Â¶ÇÊûúÊ≤°ÊúâËÅäÂ§©ËÆ∞ÂΩïÔºåÂàôÊòæÁ§∫ÁÉ≠Èó®ÈóÆÈ¢ò
      if (this.chatMessages.length === 0) {
        this.showHotQuestions = true;
      }
    }
  }
}
</script>

